.edubox{
  height:auto;
  width:300px;
  margin:15px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 45px;
  border: 5px solid black ;
  padding: 20px;
  text-align: center;
  font-family:monospace;
  animation-name: myAnimation;
  animation-duration: 1s;

  
  
}

  
.edubox1{
  height:auto;
  width:300px;
  margin-left:5px;
  margin-top:60px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 45px;
  border: 5px solid black ;
  padding: 20px;
  text-align: center;
  font-family:monospace;
  animation-name: myAnimation;
  animation-duration: 1s;

  
  
}

.edubox:hover{
 animation: boxtrans 8s normal forwards;
}
@keyframes boxtrans{
  
  100%{background-color: rgb(9, 189, 212);border-radius: 80px;}
}

@keyframes myAnimation{
  0%{
    display: none;
    opacity: 0;
    
  }
  
  100%{
   
    opacity: 1;
  
  }
}



