@keyframes trans {
  
  50% {border-radius:30px;}
  100%{border-radius:50px;color: black; background-color: white;
       border: 2px black solid}

}

.progress {
  background: black;
  justify-content: flex-start;
  border-radius: 100px;
  align-items: center;
  position: relative;
  margin-left: 18% ;
  display: flex;
  height: 15px;
  width: 200px;
}

.progress-value {
  animation: load 3s normal forwards;
  
  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}

@keyframes load {
  0% { width: 0; }
  
  100% { width: 80%; background-color: chartreuse;}
}


.progress-value1 {
  animation: load1 3s normal forwards;
  
  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}


@keyframes load1 {
  0% { width: 0; }
  
  100% { width: 100%; background-color: green;}
}



.progress-value2 {
  animation: load2 3s normal forwards;
  
  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}


@keyframes load2 {
  0% { width: 0; }
  
  100% { width: 70%; background-color: orange;}
}



.progress-value3 {
  animation: load3 3s normal forwards;
  
  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}


@keyframes load3 {
  0% { width: 0; }
  
  100% { width: 50%; background-color: yellow;}
}

